<template>
  <v-sheet>
    <v-container>
        <base-material-card
            color="green"
            class="autoWidthCard"
        >
            <template v-slot:heading>
                <div class="display-1 font-weight-light">
                    Batches
                </div>
            </template>
            <div class="text-center">
                <v-icon class="largeIcon primary--text">mdi-view-sequential</v-icon>
            </div>
            <h1 class="text-center">Batches</h1>
            <p class="text-center text--secondary mt-3">Batches allow you to schedule VALUE SERP Searches to run on-mass.</p>

            <div class="listDiv mt-5">
                <h3>Here's how:</h3>
                <p class="mt-5">
                    <v-icon class="mr-2 primary--text">mdi-check-circle-outline</v-icon>Add up to 15,000 Searches per Batch</p>
                <p>
                    <v-icon class="mr-2 primary--text">mdi-check-circle-outline</v-icon>Schedule the Batch to run monthly, weekly, daily or hourly</p>
                <p>
                    <v-icon class="mr-2 primary--text">mdi-check-circle-outline</v-icon>Get results as JSON, CSV or via the Batches API</p>
                <p>
                    <v-icon class="mr-2 primary--text">mdi-check-circle-outline</v-icon>Get notified of new results via email or webhook POST</p>
            </div>

            <div class="w-100 text-center mt-3">
                <v-btn color="blue" @click.stop="drawer = !drawer">
                    <v-icon>mdi-plus</v-icon>
                    Add Batch
                </v-btn>
            </div>
        </base-material-card>
    </v-container>
    
    <v-navigation-drawer
      v-model="drawer"
      fixed
      temporary
      right
      width="500"
    >
        <v-list-item>
            <v-list-item-content>
                <v-list-item-title class="display-1 blue--text font-weight-bold">Add New Batch</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <div class="pa-3">
            <v-row>
                <v-col md="8">
                    <v-text-field
                        label="Name"
                        outlined
                        dense
                    ></v-text-field>
                </v-col>
                <v-col md="4">
                    <v-switch
                        inset
                        style="margin-top: 0"
                        color="blue"
                    ></v-switch>
                </v-col>
            </v-row>

            <div class="alignCenter">
                <span class="display-1 mr-2 blue--text">Schedule</span>
                <v-divider></v-divider>
            </div>
            <v-radio-group
                v-model="schedule"
                row
            >
                <v-radio
                    label="Daily"
                    value="radio-1"
                    color="blue"
                ></v-radio>
                <v-radio
                    label="Weekly"
                    value="radio-2"
                    color="blue"
                ></v-radio>
                <v-radio
                    label="Monthly"
                    value="radio-3"
                    color="blue"
                ></v-radio>
                <v-radio
                    label="Manually"
                    value="radio-4"
                    color="blue"
                ></v-radio>
            </v-radio-group>
            <div>
                <v-select
                    label="Days of Month"
                    v-if="schedule == `radio-3`"
                    multiple
                    chips
                    outlined
                    dense
                ></v-select>
                <v-select
                    v-if="schedule == `radio-2`"
                    label="Days of Week"
                    multiple
                    chips
                    outlined
                    dense
                ></v-select>
                <v-select
                    v-if="schedule == `radio-1` || schedule == `radio-2` || schedule == `radio-3`"
                    label="Hours"
                    multiple
                    chips
                    outlined
                    dense
                ></v-select>
            </div>

            <div class="alignCenter">
                <span class="display-1 mr-2 blue--text">Priority</span>
                <v-divider></v-divider>
            </div>
            <v-radio-group
                v-model="schedule"
                row
            >
                <v-radio
                    label="highest"
                    value="highest"
                    color="pink"
                ></v-radio>
                <v-radio
                    label="high"
                    value="high"
                    color="warning"
                ></v-radio>
                <v-radio
                    label="normal"
                    value="normal"
                    color="success"
                ></v-radio>
                <v-radio
                    label="low"
                    value="low"
                    color="info"
                ></v-radio>
                <v-radio
                    label="lowest"
                    value="lowest"
                    color="blue"
                ></v-radio>
            </v-radio-group>

            <div class="alignCenter">
                <span class="display-1 mr-2 blue--text">Notifications</span>
                <v-divider></v-divider>
            </div>
            <p class="text--secondary mt-2">Choose the default data formats for Result Sets from this Batch. This affects the download links presented in email and webhook notifications and the format of files uploaded to your Destinations.</p>
            <div class="d-flex">
                <v-checkbox
                    class="mr-4 mt-0"
                    label="JSON"
                ></v-checkbox>
                <v-checkbox
                    class="mr-4 mt-0"
                    label="JSON Lines"
                ></v-checkbox>
                <v-checkbox
                    class="mr-4 mt-0"
                    label="CSV choose CSV fields"
                ></v-checkbox>
            </div>
            <span>Send via Email</span>
            <v-text-field
                label="Email"
                outlined
                dense
                type="email"
            ></v-text-field>      
            <p class="text--secondary">Enter an email to receive notifications, if this is left blank then the email address on your account is used instead.</p>

            <span>Webhook URL</span>
            <v-text-field
                label="Webhook URL"
                outlined
                dense
            ></v-text-field>      
            <p class="text--secondary">VALUE SERP performs a POST to your webhook when the Batch completes.Learn more about webhooks.</p>

            <span>Destinations</span>
            <v-text-field
                label="Destinations"
                outlined
                dense
            ></v-text-field>      
            <p class="text--secondary">Choose one or more Destinations (i.e. Amazon S3 buckets, Google Cloud Storage Buckets) where Result Sets from this Batch are to be sent. Create Destinations on the Destinations page.</p>
        </div>
    
        <div class="w-100 text-right mb-2">
            <v-btn color="warning" small>Cancel</v-btn>
            <v-btn color="blue" small>Save</v-btn>
        </div>
    </v-navigation-drawer>
  </v-sheet>
</template>

<script>

export default {
    components: {
    },

    data:()=> ({
        drawer: null,
        items: [
          { title: 'Home', icon: 'mdi-view-dashboard' },
          { title: 'About', icon: 'mdi-forum' },
        ],
        schedule: null,
    })
}
</script>

<style>
    .autoWidthCard .v-card--material__heading {
        width: auto !important;
    }

    .largeIcon {
        font-size: 120px !important;
    }

    .listDiv {
        max-width: 700px;
        margin: auto;
    }

    .w-100 {
        width: 100%
    }

    .alignCenter {
        display: flex;
        align-items: center;
    }
</style>